import Vue from 'vue'
import Router from 'vue-router'

// Pages
import HomePage from '@/pages/Home';
import ContactPage from '@/pages/Contact';
// LAYOUTS
// import Layout from '@/layouts/guest.layout';
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/contact',
      name: 'ContactPage',
      component: ContactPage
    }

  ]
})



