<template>
	<div>
		<div class="hero is-fullheight purple">
			<div class="hero-head">
				<navbar selected="contact"></navbar>
			</div>
			<div class="hero-body">
				<div class="container">
					<p class="title 3 has-text-centered has-text-white">
						My social networks
					</p>
					<br>
					<div class="columns has-text-white">
						<div class="column is-4 has-text-centered item-card-contact">
							<center>
								<img class="icon-img" src="../assets/facebook-clipart-round-5.png">
							</center>
						</div>
						<div class="column is-4 has-text-centered item-card-contact">
							<center>
								<img class="icon-img" src="../assets/instagram-round-flat-512.png">
							</center>
						</div>
						<div class="column is-4 has-text-centered item-card-contact">
							<center>
								<img class="icon-img" src="../assets/2000px-GitHub_font_awesome.svg.png">
							</center>
					</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import navbar from '../components/navbar.vue';
	export default{
		mounted(){
			document.querySelector('html').style['overflow-y'] = 'scroll';
		},
		components:{
			navbar
		}
	}
</script>
<style lang="scss">
	@media screen and (max-width: 768px)
	{
		.item-card-contact{
			min-height: 20vh !important;
			padding: 20px !important;
			.icon-img{
				width: 50%;
			}
		}
		body{
		}
	}
	.item-card-contact{
		height: 50vh;
		.icon-img{
			max-width: 250px;
			cursor: pointer;

		}
	}
	.container{
		padding-top: 30px;
		overflow-y: hidden;
		overflow-x: hidden !important;
	}
</style>


