import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
export const store = new Vuex.Store({
	state:{
		navbar:{
			active:false,
			clases:'animated fadeInRight faster'
		}
	},
	getters:{
		getNavbarState(state)
		{
			return state.navbar;
		}
	},
	mutations:{
		setNavbarState(state,obj)
		{
			state.navbar = obj;
		}
	}
});


