<template>
	<div>
		<div class="hero is-fullheight purple">
			<div class="hero-head">
				<navbar selected="home"></navbar>
			</div>
			<div class="hero-body">
				<div class="container has-text-centered has-text-white">
					<center>
						<div class="diapositiva diapositiva1 animated fadeInDown" v-show="selected==1">
							<figure class="image my-image">
						  		<img src="../assets/coding.png">
							</figure>
							<br>
							<p class="title is-3 has-text-white">Web Developer</p>
						</div>
						<div class="diapositiva diapositiva2 animated zoomIn" v-show="selected==2">
							<br>
							<p class="title is-3 has-text-white">Información personal</p>
							<p class="title is-4 has-text-white">
								Mi nombre es Lautaro Umpierrez, soy desarrollador frontend & backend.
								Vivo en Maldonado, Uruguay y tengo una personalidad muy extrovertida, soy capaz de trabajar en solitario, pero me entusiasma tener un work team.
							</p>
						</div>
						<div class="diapositiva diapositiva3 animated lightSpeedIn" v-show="selected==3">
							<br>
							<p class="title is-3 has-text-white">Mi stack de tecnologías</p>
							<br>
							<p>
								<span class="tag is-medium is-success">
									<span class="icon">
										<i class="fab fa-vuejs"></i>
									</span>&nbsp; Vue.js
								</span>
								<span class="tag is-medium is-info">
									<span class="icon">
										<i class="fab fa-laravel"></i>
									</span>&nbsp; Laravel
								</span>
								<span class="tag is-medium is-danger">
									<span class="icon">
										<i class="fab fa-css3-alt"></i>
									</span>&nbsp; CSS3
								</span>
								<br><br>
								<span class="tag is-medium is-warning">
									<span class="icon">
										<i class="fab fa-js-square"></i>
									</span>&nbsp; Javascript
								</span>
								<span class="tag is-medium is-warning">
									jQuery
								</span>
								<br><br>
								<span class="tag is-medium is-link">
									Bootstrap versión 3 y 4
								</span>
								<span class="tag is-medium is-primary">
									<span class="icon">
										<i class="fab fa-sass"></i>
									</span> &nbsp; SASS
								</span>
								<br>
								<br>
								<span class="tag is-medium is-danger">
									Codeigniter 3
								</span>
								<span class="tag is-medium is-info">
									<span class="icon">
										<i class="fab fa-wordpress"></i>
									</span> &nbsp; Wordpress
								</span>


							</p>
						</div>
						<div class="diapositiva diapositiva4 animated flipInX" v-show="selected==4">
							<p class="title is-3 has-text-white">
								Proyectos en los que he participado
							</p>
							<figure class="image is-128x128">
							  <img src="https://bulma.io/images/placeholders/256x256.png">
							</figure>
							<figure class="image is-128x128">
							  <img src="https://bulma.io/images/placeholders/256x256.png">
							</figure>
						</div>
						<!-- <div class="diapositiva5 animated slideInUp" v-show="selected==5">
							<br>
							<p class="title is-3 has-text-white">Another Data</p>
						</div> -->
					</center>
				</div>
			</div>
			<div class="arrows">
				<div class="arrow-left" @click="selectPrevious()" v-bind:class="arrows.left.clases">
					<i class="fas fa-angle-left"></i>
				</div>
				 <div class="arrow-right" @click="selectNext()"  v-bind:class="arrows.right.clases">
					<i class="fas fa-angle-right"></i>
				</div>
			</div>
			<div class="dots">
				<center>
					<div class="dot faster" @click="selectThis(1)" v-bind:class="[selected==1 ? 'selected animated bounce' : '']"></div>
					<div class="dot faster" @click="selectThis(2)" v-bind:class="[selected==2 ? 'selected animated bounce' : '']"></div>
					<div class="dot faster" @click="selectThis(3)" v-bind:class="[selected==3 ? 'selected animated bounce' : '']"></div>
					<div class="dot faster" @click="selectThis(4)" v-bind:class="[selected==4 ? 'selected animated bounce' : '']"></div>
					<!-- <div class="dot faster" @click="selectThis(5)" v-bind:class="[selected==5 ? 'selected animated bounce' : '']"></div> -->
				</center>
			</div>
		</div>
	</div>
</template>
<script>
	import navbar from '../components/navbar.vue';
	export default{
		components:{navbar},
		mounted()
		{
			document.querySelector('html').style['overflow-y'] = 'hidden';
			this.startTheCount();
		},
		data:()=>({
			selected: 1,
			diapositivesNumber: 4,
			counterTime: 15000,
			arrows:{
				left:{
					clases:'',
				},
				right:{
					clases:'',
				}
			}
		}),
		methods:{
			selectThis(number)
			{
				this.resetCounterTime();
				if(this.selected != number) this.selected = number;
			},
			selectPrevious()
			{
				this.arrows.left.clases = 'animated wobble faster';

				setTimeout(()=>this.arrows.left.clases = '',500);
				this.resetCounterTime();
				if(this.selected > 1) 
					this.selected = this.selected - 1;
				else
					this.selected = this.diapositivesNumber;
			},
			selectNext()
			{
				this.arrows.right.clases = 'animated wobble faster';

				setTimeout(()=>this.arrows.right.clases = '',500);
				this.resetCounterTime();
				if(this.selected < 4)
					this.selected = this.selected+1;
				else
					this.selected = 1;

			},
			startTheCount()
			{
				var thisObj = this;
				window.setInterval(()=>{
					if(thisObj.counterTime == 0)
					{
						thisObj.selectNext();
						thisObj.resetCounterTime();
					}else{
						thisObj.counterTime = thisObj.counterTime - 1000;
					}
				},1000);
			},
			resetCounterTime()
			{
				this.counterTime = 15000;
			}
		}
	}
</script>
<style lang="scss">
	@media screen and (max-width: 768px)
	{
		.dots{
			width: 100% !important;
			left: 0% !important;
			padding: 5px !important;
			bottom: 0px !important;
			background-color: #8e44ad !important;
		}
		.container{
			padding-bottom: 30px !important;
			overflow-y: scroll !important;
			overflow-x: hidden !important;
		}
	}
	.purple
	{
		background-color: #8e44ad !important;
	}
	.my-image
	{
		width: 230px;
		height: 230px;
	}
	.dots{
		position: absolute;
		bottom: 50px;
		width: 25%;
		padding-top: 20px;
		left: 37.5%;
		.dot
		{
			transition: 0.3s;
			width: 20px;
			height: 20px;
			border-radius: 20px;
			display: inline-block;
			margin: 5px;
			cursor: pointer;
			position: relative !important;
			background-color: #9b59b6;
		}
		.dot.selected
		{
			transition: 0.3s;
			cursor: default;
			background-color: #8c7ae6 !important;
		}
	}
	div.arrows{
		i{
			font-size: 40px;
			cursor: pointer;
			transition: 0.3s;
			color: #fff;
			&:hover{
				transition: 0.3s;
				transform: scale(1.6);
			}
		}
		div.arrow-left{
			position: fixed;
			left: 10px;
			top: calc(50% - 20px);
		}
		div.arrow-right{
			position: fixed;
			right: 10px;
			top: calc(50% - 20px);
		}
	}
	.diapositiva4{
		.image{
			display: inline-block !important;
			margin: 10px !important;
		}
	}
</style>


