<template>
	<div class="navbar-container-lautaumpierrez">
		<div class="activator" @click="openNavbar()">
			<i class="fas fa-bars"></i>
		</div>
		<div class="bg-navbar-lautaumpierrez" @click="closeNavbar()" v-show="navbar.active"></div>
		<div class="navbar-lautaumpierrez" v-bind:class="navbar.clases" v-show="navbar.active">
			<div class="top"></div>
			<div class="menu">
			  <ul class="menu-list">
			    <li @click="goTo({name:'HomePage'})" v-bind:class="[selected == 'home' ? 'active': '']"><a>Home | Presentation</a></li>
			    <li @click="goTo({name:'ContactPage'})" v-bind:class="[selected == 'contact' ? 'active' : '']"><a>Contact | Social Networks</a></li>
			  </ul>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		props:['selected'],
		data:()=>({
			
		}),
		methods:{
			goTo(param)
			{
				var thisObj = this;
				this.closeNavbar();
				setTimeout(()=>{
					thisObj.$router.push(param);
				},600);
			},
			openNavbar()
			{
				this.$store.commit('setNavbarState',{active: true,clases:'animated fadeInRight faster'});
			},
			closeNavbar()
			{
				this.$store.commit('setNavbarState',{active:true,clases:'animated fadeOutRight faster'});
				setTimeout(()=>this.$store.commit('setNavbarState',{active:false, clases:'animated fadeInRight faster'}),500);

			}
		},
		computed:{
			navbar()
			{
				return this.$store.getters.getNavbarState;
			},

		}
	}
</script>
<style lang="scss">
	@media screen and (max-width: 768px)
	{
		.navbar-container-lautaumpierrez
		{
			.navbar-lautaumpierrez
			{
				width: 95% !important;
			}
		}
	}
	.navbar-container-lautaumpierrez{
		.activator
		{
			position: absolute;
			top: 15px;
			right: 15px;
			cursor: pointer;
			i{
				font-size: 25px;
				color: #fff !important;
			}
		}
		.navbar-lautaumpierrez{
			position: fixed;
			width: 25%;
			height: 100%;
			background-color: rgba(255,255,255,1);
			top: 0;
			z-index: 101;
			right: 0;
			.top{
				height: 20px;
			}
			.menu
			{
				.menu-list
				{
					li.active{
						background-color: rgb(240,240,240) !important;
					}
					li
					{
						a{
							transition: 0.3s;
							padding-top: 20px;
							padding-bottom: 20px;
							&:hover{
								transition: 0.3s;
							}
						}
					}
				}
			}
		}
		.bg-navbar-lautaumpierrez
		{
			cursor: pointer;
			position: fixed;
			width: 100%;
			background-color: rgba(51,51,51,0.8);
			height: 100%;
			z-index: 100;
			top: 0;
			left: 0;
		}
	}
</style>


